@import '~@yandex-music-int/sol/mixins/aspectRatio.scss';
@import '~@yandex-music-int/sol/mixins/breakpoints.scss';
@import '~@yandex-music-int/sol/mixins/supportHasSelector.scss';

.root {
    display: inline-flex;

    width: 100%;
    min-width: 140px;
}

.controls {
    position: absolute;

    inset-block-start: 0;
    inset-inline-start: 0;

    opacity: 0;

    @include mediaBreakpointDown($ym-breakpoint-xs) {
        display: none;
    }
}

.cover {
    display: block;

    &:hover {
        .controls {
            opacity: 1;

            background-color: var(--ym-surface-color-primary-enabled-entity);
        }
    }

    &:has(:focus-visible) {
        box-shadow: 0 0 0 var(--ym-spacer-size-xs) var(--ym-outline-color-primary-disabled);

        .controls {
            opacity: 1;

            background-color: var(--ym-surface-color-primary-enabled-entity);
        }
    }

    @include supportHasSelector() {
        &:focus-within {
            box-shadow: 0 0 0 var(--ym-spacer-size-xs) var(--ym-outline-color-primary-disabled);

            .controls {
                opacity: 1;

                background-color: var(--ym-surface-color-primary-enabled-entity);
            }
        }
    }
}

.coverLink {
    display: block;
    position: relative;

    outline: none;
}

.image {
    width: 100%;

    @include aspectRatio(1, 1);
}

.titleLink {
    color: var(--ym-controls-color-primary-text-enabled_variant);

    &:hover,
    &:focus-visible {
        text-decoration: underline;
    }
}

.artists {
    max-height: var(--ym-font-line-height-label-s);

    line-height: var(--ym-font-line-height-label-s);

    color: var(--ym-controls-color-primary-text-enabled);
}

.artistLink {
    color: inherit;

    &:hover,
    &:focus-visible {
        color: var(--ym-controls-color-primary-text-enabled_variant);
    }
}

.playButton {
    color: var(--ym-controls-color-secondary-text-enabled_variant);
}

.control {
    will-change: transform;

    &:hover,
    &:focus-visible {
        transform: scale(1.05);
    }

    @media screen and (prefers-reduced-motion: reduce) {
        transition: none;
    }
}
