@mixin averageColorBackground() {
    position: absolute;
    inset-block-start: 0;
    inset-inline-start: 0;

    transition: opacity var(--ym-duration-transition), background-color var(--ym-duration-transition);

    opacity: 0;

    background: linear-gradient(
        var(--average-color-background, var(--ym-background-color-secondary-enabled-blur)) 0%,
        transparent 100%
    );

    width: 100%;
    height: 100%;
    max-height: 400px;

    @media screen and (prefers-reduced-motion: reduce) {
        transition: none;
    }
}
