$ym-breakpoint-xxxxs: 320;
$ym-breakpoint-xxxs: 480;
$ym-breakpoint-xxs: 768;
$ym-breakpoint-xs: 960;
$ym-breakpoint-s: 1060;
$ym-breakpoint-m: 1280;
$ym-breakpoint-l: 1366;
$ym-breakpoint-xl: 1920;
$ym-breakpoint-xxl: 2560;
$ym-breakpoint-xxxl: 3840;

@mixin mediaBreakpointUp($width, $andEqual: true) {
    @media only screen and (min-width: $width + if($andEqual, 0, 0.02) + 'px') {
        @content;
    }
}

@mixin mediaBreakpointDown($width, $andEqual: true) {
    @media only screen and (max-width: $width - if($andEqual, 0, 0.02) + 'px') {
        @content;
    }
}
